.list{
	color: #fff;
	width: 300px;
	background-color: #bf07ac;
	height: 50px;
	margin: 20px auto;
	border-radius: 5px;
}

.list p{
	padding: 10px;	
}

.list p span{	
	margin-left: 20px;
	cursor: pointer;
}

.faicons{
	margin: 0 5px ;
	font-size: 15px;
}
.list p input{
	background-color: transparent;
	border: #fff;
	font-size: 18px;
 	color: white;
}

.list p input:focus{
	outline: none;
}

#trash{
	background-color: transparent;
	border: 0;
	color: white;
	font-size: 15px;
}